export default class PopUpManager {
    constructor(_options){
        this.parameter = _options.parameter;

        this.popUp = document.getElementById('popUp');
        this.popUpText = document.getElementById('popUp-text');
        this.buttonText = document.getElementById('button-text');

        this.currentText = 1;

        this.setPopUpText1()
    }

    setPopUpText1(){
        this.popUpText.innerHTML = "Vous y tes !<br><b>La bote de Ptri n7.</b><br><br>Et effectivement, il y a beaucoup de bactries. <br><br>Prenez le contrle des antibiotiques et interceptez les bactries. <br><br><b>Restez prudent, certaines ractions des bactries pourraient vous surprendre.</b>"
        this.buttonText.innerHTML = "Niveau 1"
    }

    setPopUpText2(){
        this.popUpText.innerHTML = "<b>Mcanismes de rsistances des bactries :</b><br><br><b>- Enzymes : Inactivation de l'antibiotique</b> par une enzyme bactrienne.<br>(Une enzyme est une protine prsente dans les cellules et qui a pour fonction de faciliter les ractions chimiques qui s'y produisent.<br><br><b>- Diminution de la quantit dantibiotique atteignant la cible</b><br>( L'antibiotique nest pas modifi, mais il ne peut pas accder  sa cible au sein de la bactrie )<br><br><b>- Impermabilit de la membrane bactrienne :</b> lantibiotique ne peut plus pntrer dans la membrane de la bactrie).<br><br><b>- Systme defflux :</b> lantibiotique est expuls activement vers lextrieur de la bactrie par des protines jouant le rle de pompe."
        this.buttonText.innerHTML = "Continuer"
    }

    setPopUpText3(){
        this.popUpText.innerHTML = "<b>Modification de la cible</b><br><br><b>-Modifications quantitatives :</b> par exemple, labsence de paroi chez les bactries du genre Mycoplasma est responsable de leur rsistance naturelle aux -lactamines.<br><br><b>-Modifications qualitatives :</b> la modification de la structure de la cible peut diminuer son affinit pour lantibiotique. Cest un mcanisme frquent de rsistance acquise.<br><br><b>-Protection de la cible :</b> cest une protection rversible de la cible (par des protines empchant la fixation des quinolones, par exemple)."
        this.buttonText.innerHTML = "Niveau 2"
    }

    changePopUpText(){

        if(this.currentText == 1){
            this.popUp.style.visibility = 'hidden'
            this.parameter.canUpdate = true;
            this.setPopUpText2()
            this.currentText += 1;
        } else if(this.currentText == 2){
           this.setPopUpText3()
           this.currentText += 1;
        } else if (this.currentText == 3){
            this.popUp.style.visibility = 'hidden'
            this.parameter.canUpdate = true;
        }


    }

}